<template>
  <div class="taobao">
    <div class="input">
      <span>淘宝店面名称:</span>
      <el-input style="width:300px;" v-model="taobaoInfo.name"></el-input>
    </div>
    <div class="input">
      <span>小二账号:</span>
      <el-input style="width:300px;" v-model="taobaoInfo.username"></el-input>
    </div>
    <div class="input">
      <span>登录密码:</span>
      <el-input type="password" style="width:300px;" v-model="taobaoInfo.password"></el-input>
    </div>
    <span style="color:red;margin-left:15px;">以上内容都为必填项</span>
    <br>
    <el-button
      style="float:right;margin-right:15px;width:200px;margin-bottom:20px;"
      type="primary"
      v-if="!isStart"
      @click="startYz()"
    >验证账号</el-button>
    <el-progress
      :text-inside="false"
      :stroke-width="18"
      :percentage="rate"
      v-show="isStart"
      style="margin-top:30px;"
    ></el-progress>
  </div>
</template>

<script>
import axios from 'axios'
// import tipInput from '@/components/TipInput/tipInput'
export default {
  data () {
    return {
      taobaoInfo:{
          name:"",
          username:"",
          password:""
      },
      isStart:false,
      rate:0
    }
  },
  created () {
    
  },
  methods:{
      startYz(){
        var that = this
        if(!that.taobaoInfo.name){
          this.$message({
            message: '请输入淘宝店面名称',
            type: 'warning'
          });
          return false
        }
        if(!that.taobaoInfo.username){
          this.$message({
            message: '请输入小二账号',
            type: 'warning'
          });
          return false
        }
        if(!that.taobaoInfo.password){
          this.$message({
            message: '请输入登录密码',
            type: 'warning'
          });
          return false
        }
          that.isStart = true
          var num = 0
          var start = setInterval(function(){
              num += 1
              that.rate = num
              if(num == 100){
                  clearInterval(start)
                  setTimeout(function(){
                  that.$router.push({path:'/Register/success'})
                  },500)
              }
          },100)
      }
    }
}
</script>

<style lang="scss" scoped>
.taobao{
    margin-top: 20px;
    width: 500px;
    .input{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 15px;
    }
} 
</style>
<style>
.headerWrapper .el-dropdown{
  color: #000;
  font-size: 16px;
}
</style>



